import React, { useEffect, useState } from 'react'

const UserList = () => {

    const [users, setUsers] = useState([]);

    const getUsers = async () => {
        const response = await fetch('https://api.covid19api.com/summary');
        const data = await response.json()
        setUsers(data.Countries);
    }

    useEffect(() => {
        getUsers();
    }, [])

    return (
        <>
            <h2>Country wise COVID cases:-</h2>
            <div className="container-fluid">
                <div className="row text-center">
                    {users.map((curElem) => {
                        return (
                            <div className="col-10 col-md-4 mt-2" key={curElem.ID}>
                                <div className="card p-2">
                                    <div className="d-flex align-items-center">
                                        <div className="ml-3 w-100">
                                            <h4 className="mb-0 mt-0 textLeft">{curElem.Country}</h4><span className="textLeft">Country code: {curElem.CountryCode}</span>
                                            <div className="p-2 mt-2 bg-primary d-flex justify-content-between rounded text-white status">
                                                <div className="d-flex flex-column"><span className="articles">Total Confirmed</span> <span className="number1">{curElem.TotalConfirmed}</span></div>
                                                <div className="d-flex flex-column"><span className="followers">Total Deaths</span> <span className="number2">{curElem.TotalDeaths}</span></div>
                                                <div className="d-flex flex-column"><span className="rating">Total Recovered</span> <span className="number3">{curElem.TotalRecovered}</span></div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>
        </>
    )
}

export default UserList
